body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.footer {
  width: 100%;
  text-align: center;
  background: #5bccee;
  color: #fff;
  padding:5px 0;
   
}

.footer a {
  font-weight: bold;
  text-decoration:none;
  color: #10404e;
}
.social-icons ul li {
  display: inline-block;
  text-align: center;
  border: 1px solid #000;
  width: 36px;
  height: 36px;
  background: #000;
  line-height: 36px;
  margin: 0px 15px;
  border-radius: 50px;
  transition: 0.5s all;
  -webkit-transition: 0.5s all;
  -moz-transition: 0.5s all;
  -o-transition: 0.5s all;
  -ms-transition: 0.5s all;
}
.social-icons ul li a span {
  color: #fff;
}

.social-icons ul li:hover {
  background: #5bccee;
  color: #fff;
}
@media (max-width: 640px) {
  .social-icons ul li {
    margin: 0px 11px;
  }
}

@media (max-width: 400px) {
  .social-icons ul li {
    margin: 0px 8px;
  }
}
@media (max-width: 350px) {
  .social-icons ul li {
    margin: 0px 4px;
  }
}
@media (max-width: 310px) {
  .social-icons ul li {
    margin: 0px 2px;
  }
}

.headdir {
  background: #5bccee;
  width: 100%;
  padding: 0;
  max-height: 85px;
  color: white;
}
.logo {
  float: left;
  padding-left: 8px;
  margin-top: -10px ;
}
.clearfix:before, .clearfix:after {
  content: "";
  display: table;
}

.clearfix:after {
  clear: both;
}

.clearfix {
  *zoom: 1;
}
.logo .yellow {
  color: #10404e;
}
.triger-nav {
  float: right;
  border: 1px solid white;
  padding: 10px;
  margin : 18px 10px 0 0;
  border-radius: 8px;
}
.triger-nav a{
  text-decoration: none;
  color:  #10404e;
  font-weight: 500;
}
.triger-nav:hover {
  background-color: #757573de;
}


@media (min-width: 1066px) {
  .headir_in {
    max-width: 1050px;
    margin-left: 150px;
  }
  .triger-nav {
    margin : 23px 10px 0 0;
  }
}
@media (min-width: 1466px) {
  .headir_in {
    max-width: 1050px;
    margin-left: 300px;
  }
}


.app {
  text-align: center;
  min-height: 80vh;
  background-color: #282c34;
  color: white;
  align-items: center;
  justify-content: center;
  padding: 0 10px;
}

.app p{
  text-align: justify;
}

.container {
  margin: 0px;
  padding: 0px;
}


.img_container{
margin-bottom: 8px;
border-radius: 10px;
padding : 20px;
border: 1px solid white;
}

.img_container img {
  width: 90%;
  border-radius: 14px;
}

.hasil {
  color: #d4f70a;
  font-size: 1.5rem;
  font-weight: bold;
}

.card{
  border: 1px solid white;
  border-radius: 8px;
}

.card li{
  text-align: left;
}
.button{
  background: #282c34;
  color: white;
  padding: 10px;
  border-radius: 8px;
  margin-top: 10px;
  border: 1px solid white;
}

.button:hover{
  background: #5bccee;
}

.input{
  background: white;
  margin-top: 10px;
  color: black;
  padding: 10px;
  max-width: 127px;
  border-radius: 8px;
  border: 1px solid white;
}

.input:focus{
  background: #282c34;
  color: white;

}

@media (min-width: 566px) {
  .app {
    padding: 0 40px;
  }
}
@media (min-width: 746px) {
  .app {
    padding: 0 130px;
  }
}

@media (min-width: 946px) {
  .app {
    padding: 0 230px;
  }
}

@media (min-width: 1246px) {
  .app {
    padding: 0 350px;
  }
}
@media (min-width: 1446px) {
  .app {
    padding: 0 530px;
  }
}

